<template>
   <v-card class="pa-0" @click="goToStockLookup(stock.id)">
        <div v-if="stock.Alert == 'STRONG LONG ENTRY'">
        <v-row justify="space-around">
            <v-card-title>{{ stock.id }}</v-card-title>
            <v-card-title class="text-subtitle-1 text-center">{{ stock.Alert }}<br>{{ stock.Price  | toCurrency()}}</v-card-title>
        </v-row>
        <v-card-text class="primary white--text">
            <v-row justify="space-around" class="py-0">
            <v-card-subtitle class="text-center">RSI:</v-card-subtitle>
            <v-card-subtitle class="text-center">{{ stock.RSI }}</v-card-subtitle>
            </v-row>
            <v-row justify="space-around" class="py-0">
            <v-card-subtitle class="text-center">stochK:</v-card-subtitle>
            <v-card-subtitle class="text-center">{{ stock.StochK }}</v-card-subtitle>
            
            <v-card-subtitle class="text-center">stochD:</v-card-subtitle>
            <v-card-subtitle class="text-center">{{ stock.StochD }}</v-card-subtitle>
            </v-row>
        </v-card-text>
        </div>
    </v-card>
</template>

<script>
export default {
    name: 'updateCard',
    props: {
        stock: { 
            type: Object,
            required: true
        }
            
    },
    filters: {
    toCurrency(amount) {
      var formatter = new Intl.NumberFormat('en-US', {
        style: 'currency',
        currency: 'USD',
      });
      return formatter.format(amount)
    },    
  },
  methods: {
    goToStockLookup(stock){
      this.$router.push({ name: 'Stock', params: { id: stock } })
    },   
  },
  watch: {
      stock: function() {
        //   console.log(this.stock)
          this.$store.dispatch('addDBAlert', this.stock)
      }
  }
    

}
</script>

